@import '../Variables/Variables.scss';

@mixin planet {
    display: flex;
    height: 30rem;
    flex-direction: column;
    align-items: center;
}

@mixin planet__title {
    font-size: $fifty-six;
    color: $white;
    font-family: $bellefair;
}

@mixin planet__description {
    font-size: $fifteen;
    color: $light-blue;
    font-family: $barlow;
    text-align: center;
    line-height: 2.5rem;
    margin-bottom: 3rem;
}

@mixin planet__line {
    width: 100%;
    border: 1px solid $dark-grey;
}

@mixin planet__travel__distance {
    width: 15rem;
    margin-top: 2rem;
    text-align: center;
}

@mixin planet__travel__distance__heading {
    font-size: $fourteen;
    color: $light-blue;
    font-family: $barlow-condensed;
    padding-bottom: 1rem;
}

@mixin planet__travel__distance__number {
    font-size: $twenty-eight;
    color: $white;
    font-family: $bellefair;
}

@mixin planet__travel__time {
    width: 15rem;
    text-align: center;
    margin-top: 2rem;
}

@mixin planet__travel__time__heading {
    font-size: $fourteen;
    font-family: $barlow-condensed;
    color: $light-blue;
    padding-bottom: 1rem;
}

@mixin planet__travel__time__number {
    font-size: $twenty-eight;
    font-family: $bellefair;
    color: $white;
}

// Media Query Tablet
@mixin for-tablet__planet {
    @media screen and (min-width: 768px) {
        width: 55%;
    }
}

@mixin for-tablet__planet__title {
    @media screen and (min-width: 768px) {
        font-size: $eighty;
    }
}

@mixin for-tablet__planet__description {
    @media screen and (min-width: 768px) {
        font-size: $sixteen;
    }
}

@mixin for-tablet__planet__travel {
    @media screen and (min-width: 768px) {
        display: flex;
        margin-top: 3rem;
        width: 80%;
        justify-content: space-evenly;
    }
}

// Media Query Desktop
@mixin for-desktop__planet {
    @media screen and (min-width: 1440px) {
        width: 28%;
        align-items: flex-start;
    }
}

@mixin for-desktop__planet__title {
    @media screen and (min-width: 1440px) {
        font-size: $onehundered;
    }
}

@mixin for-desktop__planet__description {
    @media screen and (min-width: 1440px) {
        text-align: start;
        font-size: $eighteen;
    }
}

@mixin for-desktop__planet__travel {
    @media screen and (min-width: 1440px) {
        justify-content: flex-start;
    }
}

@mixin for-desktop__planet__travel__distance {
    @media screen and (min-width: 1440px) {
        margin-right: 6rem;
    }
}

@mixin for-desktop__planet__travel__distance__heading {
    @media screen and (min-width: 1440px) {
        display: flex;
    }
}

@mixin for-desktop__planet__travel__distance__number {
    @media screen and (min-width: 1440px) {
        width: 18rem;
        display: flex;
    }
}

@mixin for-desktop__planet__travel__time__heading {
    @media screen and (min-width: 1440px) {
        display: flex;
    }
}

@mixin for-desktop__planet__travel__time__number {
    @media screen and (min-width: 1440px) {
        width: 18rem;
        display: flex;
    }
}